<template>
  <div>
      <div style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
    
          <div style="display: inline">
            <el-input
              placeholder="通过工序名搜索,记得回车哦..."
              clearable
              @change="keywordsChange"
              style="width: 300px;margin: 0px;padding: 0px;"
              size="mini"
              :disabled="advanceSearchViewVisible"
              @keyup.enter.native="searchEmp"
              prefix-icon="el-icon-search"
              v-model="keywords">
            </el-input>
            <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
      
            </el-button>
          </div>
          <div style="margin-left: 5px;margin-right: 20px;display: inline">
            <el-button type="primary" @click="addAndFlushEmp" size="mini" icon="el-icon-plus">添加</el-button>
          </div>
      </div>

    <el-dialog v-dialogDrag :title="dialogTitle"
               style="padding: 0px;"
               :close-on-click-modal="false"
               :visible.sync="dialogFormVisible"
               @close="cancelOpt"
               width="600px">
      <div style="width: 100% "v-if="dialogFormVisible">
        <el-form :model="process" :rules="rules" ref="addEmpForm" size="small" label-width="90px">
          <el-form-item label="名称:" prop="name">
            <el-input prefix-icon="el-icon-edit" v-model="process.address" size="mini" style="width: 200px"
                      placeholder="工序名称"></el-input>
          </el-form-item>
          <el-transfer
            style="text-align: left; display: inline-block"
            filterable
            :filter-method="filterMethod"
            filter-placeholder="工序名称"
            :titles="['工序顺序', '工序步骤']"
            :button-texts="['添加', '移除']"
            v-model="value2"
            :data="data2">
          </el-transfer>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible=false">取 消</el-button>
        <el-button type="primary" @click="add('addEmpForm')">确 定</el-button>
      </div>
    </el-dialog>
    <el-table :data="processs" style="width: 100%">
      <el-table-column
        type="selection"
        align="left"
        v-loading="tableLoading"
        width="30">
      </el-table-column>
      <el-table-column
        prop="name"
        align="left"
        fixed
        label="名称"
        width="120">
      </el-table-column>
      <el-table-column
        prop="mks"
        width="120"
        align="left"
        label="备注">
      </el-table-column>
      <el-table-column
        align="left"
        label="操作"
        width="195">
        <template slot-scope="scope">
          <el-button @click="showProcessView(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                     size="mini">查看工序
          </el-button>
          <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 4px 3px 4px;margin: 2px"
                     size="mini">编辑
          </el-button>
          <el-button type="danger" style="padding: 3px 4px 3px 4px;margin: 2px" size="mini"
                     @click="deleteData(scope.row)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="display: flex;justify-content: flex-end;margin: 2px">
      <el-pagination
        background
        :page-size="10"
        :pager-count="11"
        :current-page="currentPage"
        @current-change="currentChange"
        layout="prev, pager, next"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>

</template>
<script>
  export default {
    data() {

      const item = {

      };
      return {
        showOrHidePop: false,
        defaultProps: {
          label: 'plateNumber',
          isLeaf: 'leaf',
          children: 'children'
        },
        depTextColor: '#c0c4cc',
        keywords: '',
        tableLoading: false,
        advanceSearchViewVisible: false,
        rules: {
          address: [{required: true, message: '必填:地址', trigger: 'blur'}],
          carNo: [{required: true, message: '必选:车牌号', trigger: 'blur'}],
        },
        processs: [],
        dialogTitle: '',
        currentPage: 1,
        totalCount: -1,
        cars:[],
        process: {
          carNo: '',
          address: '',
          maintainDate: '',
          nowMileage: '',
          mks: ''
        },
        dialogTableVisible: false,
        dialogFormVisible: false,
      }
    },
    mounted: function () {

      this.initData();
      var _this=this;
      this.getRequest("/vehiclerecord/baseData").then(resp => {
        if (resp && resp.status == 200) {
          var data = resp.data;
          console.log(data);
          _this.cars = data.root.cars;

        }
      })
    },

    methods: {
      searchEmp(){
        this.loadData();
      },
      cancelOpt(){
        this.dialogFormVisible = false;
        this.loadData();
      },
      showDepTree(){
        this.showOrHidePop = !this.showOrHidePop;
      },
      handleNodeClick(data){
        this.process.carNo = data.plateNumber;
        this.showOrHidePop = false;
        this.depTextColor = '#606266';
      },
      initData() {
        var _this = this;
        _this.loadData();
      },
      keywordsChange(val) {
        if (val == '') {
          this.loadData();
        }
      },
      currentChange(currentChange) {
        this.currentPage = currentChange;
        console.log(this.currentPage);
        this.loadData();
      },
      addAndFlushEmp() {
        this.dialogTitle = "添加";
        this.emptyData();
        this.dialogFormVisible = true;

        // var _this = this;
        // this.getRequest("/employee/basic/maxWorkID").then(resp=> {
        //   if (resp && resp.status == 200) {
        //     _this.emp.workID = resp.data;
        //   }
        // })
      },
      searchData() {
        this.loadData();
      },
      showProcessView(row){
        console.log(row)
        this.dialogTitle = "查看工序";


      },
      showEditEmpView(row) {
        console.log(row);
        this.dialogTitle = "编辑信息";
        this.process = row;
        this.process.maintainDate = this.formatDate(row.maintainDate);
        // this.employee.departmentId = row.department.id;
        delete this.process.noteMaker;
        // delete this.emp.notWorkDate;
        this.dialogFormVisible = true;
      },
      add(addEmpForm) {
        this.dialogFormVisible = true;
        var _this = this;

        this.$refs[addEmpForm].validate((valid) => {
          if (valid) {
            // this.process.maintainDate = new Date(this.process.maintainDate);
            if (this.process.id) {
              //更新
              this.tableLoading = true;
              console.log(this.process);
              this.postRequest("/vehicleRepair/addvmt", this.process).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  // var data = resp.data;
                  _this.dialogFormVisible = false;
                  _this.loadData();
                  _this.emptyData();

                }
              })
            } else {
              //添加
              this.tableLoading = true;
              console.log(_this.process);

              this.postRequest("/vehicleRepair/addvmt", this.process).then(resp => {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.dialogFormVisible = false;
                  _this.loadData();
                  _this.emptyData();

                }
              })
            }
          } else {
            return false;
          }
        });
      },
      deleteData(row) {
        this.$confirm('此操作将删除[' + row.plateNumber + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
        });
      },
      doDelete(ids) {
        this.tableLoading = true;
        var _this = this;
        this.getRequest("/vehicleRepair/delevmt?id=" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.loadData();
          }
        })
      },
      loadData() {
        var _this = this;
        this.tableLoading = true;
        this.getRequest("/vehicleRepair/findbypages?page=" + this.currentPage + "&size=10&queryName=" + this.keywords).then(resp => {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data.data;
            _this.processs = data;
            _this.totalCount = resp.data.total;
            _this.emptyData();
          }
        })
      },
      handleCommand(cmd) {
        var _this = this;
        if (cmd == 'logout') {
          this.$confirm('注销登录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            // _this.getRequest("/logout");
            // _this.$store.commit('logout');
            _this.$router.replace({path: '/'});
          }).catch(() => {
            _this.$message({
              type: 'info',
              message: '取消'
            });
          });
        }
      },
      emptyData() {
        this.process = {
          carNo: '',
          address: '',
          maintainDate: '',
          nowMileage: '',
          mks: ''
        }
      }
    },

    computed: {
      routes() {
        return this.$store.state.routes
      }
    },
    watch: {}
  }
</script>
<style>

  .el-submenu .el-menu-item {
    width: 180px;
    min-width: 175px;
  }
</style>



